.footer {
    background-color: $color-grey-dark-3;
    padding: 3rem 0;
    font-size: 1.4rem;
    color: $color-grey-light-1;

    @include respond(tab-port) {
        padding: 3rem 0;
    }

    &__logo-box {
        text-align: center;
        margin-bottom: 2rem;

        @include respond(tab-port) {
            margin-bottom: 1rem;
        }
    }

    &__content--text {
        text-align: center;

        @include respond(tab-port) {
            margin-bottom: 1.5rem;
        }
    }

    &__logo {
        width: 6rem;
        height: auto;
    }

    &__social {
        padding-top: 2rem;

        @include respond(tab-port) {
            width: 100%;
        }

        &--list {
            list-style: none;
            text-align: center;
        }

        &--item {
            display: inline;
            font-size: 3rem;

            &:not(:last-child) {
                margin-right: 1.5rem;

                @include respond(tab-port) {
                    margin-right: -.5rem;
                }
            }
        }

        &--link {
            &:link,
            &:visited {
                display: inline-block;
                color: rgba($color-grey-dark-2,0.8);
                text-decoration: none;
                margin: 0 .4rem;
                height: 4rem;
                width: 4rem;
                font-size: 1.6rem;
                line-height: 2.5;
                background-color: rgba($color-black,0.1);
                border-radius: 50px;
                -webkit-transition: all .3s ease-in-out;
                transition: all .3s ease-in-out;
            }

            &:hover,
            &:active {
                color: rgba($color-grey-dark,0.6);
                box-shadow: 0 1rem 2rem rgba($color-black, .2);
                transform: scale(1.1);
            }
        }
    }
}
