body {
    font-family: "Lato", sans-serif;
    font-weight: 400;
    /*font-size: 16px;*/
    line-height: 1.7;
    color: $color-grey-dark;
}

.heading-primary {
    color: $color-white;
    text-transform: uppercase;

    backface-visibility: hidden;
    margin-bottom: 6rem;

    &--main {
        display: block;
        font-size: 6rem;
        font-weight: 400;
        letter-spacing: 3.5rem;

        animation: moveInLeft ease-out 1s;

        @include respond(phone) {
            letter-spacing: 1rem;
            font-size: 4.5rem;
        }
    }

    &--sub {
        display: block;
        font-size: 2rem;
        font-weight: 700;
        letter-spacing: 1.74rem;

        animation: moveInRight ease-out 1s;

        @include respond(phone) {
            letter-spacing: .5rem;
        }
    }

    &--typo {
        text-align: center;
        font-size: 5.6rem;
        font-weight: 700;
        letter-spacing: 1px;
        text-transform: uppercase;
        line-height: 1.3;
        margin-bottom: 4rem;
        color: $color-white;

        @include respond(tab-land) {
            font-size: 4.5rem;
            margin-bottom: 1rem;
        }

        @include respond(phone) {
            font-size: 2.6rem;
            margin-bottom: 1rem;
        }
    }
}

.heading-secondary {
    font-size: 3rem;
    text-transform: none;
    font-weight: 700;
    display: inline-block;
    color: $color-primary;
    letter-spacing: .1rem;
    line-height: 1.3;

    @include respond(tab-port) {
        font-size: 2.5rem;
    }

    @include respond(phone) {
        font-size: 2rem;
    }
}

.heading-tertiary {
    font-size: $default-font-size;
    font-weight: 400;
    text-transform: none;
    color: $color-grey-dark-2;

    @include respond(phone) {
        text-align: center;
    }
}

.paragraph {
    font-size: $default-font-size;
    text-align: justify;

    &:not(:last-child) {
        margin-bottom: 3rem;
    }

    @include respond(phone) {
        font-size: 1.5rem;
        text-align: justify;
    }
}
