.header {
    height: 100vh;
    background-size: cover;
    background: linear-gradient(
            rgba($color-grey-dark-3, 0.8),
            rgba($color-grey-dark-3, 0.8)),
    url("../img/comp3.jpg")
    fixed round;
    position: relative;

    @include respond(phone) {
        background-size: cover;
        background: linear-gradient(
                rgba($color-grey-dark-3, 1),
                rgba($color-grey-dark-3, 1));
    }

    &__logo-box {
        position: absolute;
        top: 4rem;
        left: 50%;

        @include respond(phone) {
            top: 2rem;
            left: 43%;
        }
    }

    &__logo {
        height: 5rem;
    }

    &__text-box {
        @include absPosCenter;
        text-align: center;
        width: 70%;

        @include respond(tab-land) {
            width: 80%;
        }

        @include respond(tab-port) {
            width: 100%;
        }
    }
}

.skill-box {
    position: relative;
    height: 50rem;
    width: 100%;

    @include respond(phone) {
        padding: 0;
        height: 36rem;
    }
}

.skill-box {
    padding-top: 7rem;

    @include respond(phone) {
        padding: 0;
        margin-bottom: -2rem;
    }
}

.skill-box__text {
    font-size: 1.5rem;
    padding: 2.5rem;
    text-align: justify;

    @include respond(tab-port) {
        padding: .5rem;
    }

    @include respond(phone) {
        text-align: center;
    }
}

#myCanvas {
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
    width: auto;
    height: 100%;

    @include respond(phone) {
        left: -.8rem;
    }
}

myCanvas canvas {
    width: 500px;
}

.typewrite {
    &:after{
        content: "|";
        animation: blink .5s linear infinite alternate;
    }
}
