.project {
    position: relative;

    &__img {
        width: 100%;
        box-shadow: 0 1.5rem 4rem rgba($color-black, .4);
        border-radius: 1.5rem;
        position: absolute;
        transition: all .2s;
        outline-offset: 2rem;

        @include respond(tab-port) {
            float: left;
            position: relative;
            width: 85%;
            box-shadow: 0 1.5rem 3rem rgba($color-black, .2);
        }

        &--1 {
            left: 0;
            top: -2rem;

            @include respond(tab-port) {
                width: 100%;
                left: 3rem;
                right: 0;
                top: 2rem;
                transform: none;
            }

            @include respond(phone) {
                left: 0;
                top: 0;
            }
        }

        &:hover {
            transform: scale(1.01) translateY(-.5rem);
            box-shadow: 0 .5rem 4rem rgba($color-black, .5);
        }
    }
}
