.popup {
    height: 100vh;
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background-color: rgba($color-black, .8);
    z-index: 3000;
    opacity: 0;
    visibility: hidden;
    transition: all .3s;

    &__content {
        @include absPosCenter;

        width: 75%;
        height: 50rem;
        background-color: $color-white;
        box-shadow: 0 2rem 4rem rgba($color-black, .2);
        border-radius: 3px;
        display: table;
        overflow: hidden;
        opacity: 0;
        transform: translate(-50%, -50%) scale(.25);
        transition: all .5s .2s;
    }

    &__left {
        width: 33.3333333333%;
        display: table-cell;
    }

    &__right {
        width: 66.6666666667%;
        display: table-cell;
        vertical-align: middle;
        padding: 3rem 5rem;
    }

    &__img {
        display: block;
        width: 100%;
    }

    &__text {
        font-size: 1.4rem;
        margin-bottom: 4rem;
        text-align: justify;

        column-count: 2;
        column-gap: 4rem; //note 1em = 1.4rem = 14px of the current css selector (i.e, popup__text)
        column-rule: 1px solid $color-grey-light-2;
    }

    //Open states
    &:target {
        opacity: 1;
        visibility: visible;
    }

    &:target &__content {
        opacity: 1;
        transform: translate(-50%, -50%) scale(1);
    }

    &__close {
        &:link,
        &:visited {
            color: $color-grey-dark;
            position: absolute;
            top: 2.5rem;
            right: 2.5rem;
            font-size: 3rem;
            text-decoration: none;
            display: inline-block;
            transition: all .3s;
            line-height: 1rem;
        }

        &:hover {
            color: $color-primary;
        }
    }

    @include respond(tab-port) {
        &__content {
            position: relative;
            top: 50%;
            left: 50%;
            display: inline-block;

            width: 85%;
            height: auto;
        }

        &__left {
            width: 100%;
        }

        &__right {
            width: 100%;
            display: block;
            vertical-align: middle;
            padding: 3rem 5rem;

            @include respond(phone) {
                padding: 1rem 2rem;
            }
        }

        &__img {
            display: inline-block;
            width: 50%;
            height: 20.1rem;
            float: left;
        }

        &__text {
            font-size: 1rem;
            margin-bottom: 1rem;
            text-align: justify;

            column-count: 2;
            column-gap: 1rem; //note 1em = 1.4rem = 14px of the current css selector (i.e, popup__text)
            column-rule: 1px solid $color-grey-light-2;

            @include respond(phone) {
                font-size: .4rem;
            }
        }

        &__close {
            &:link,
            &:visited {
                color: $color-grey-dark-3;
                top: 1.5rem;
                right: 1.5rem;
            }
        }
    }
}
